.reports-list {
  background: #f9f9f9;
  border-radius: 8px;
  padding: 20px;
}

.report-item {
  background: white;
  border: 1px solid #e0e0e0;
  margin-bottom: 15px;
  border-radius: 6px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.report-header {
  display: flex;
  justify-content: space-between;
  padding: 10px 15px;
  background: #f0f0f0;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.report-content {
  padding: 15px;
  line-height: 1.6;
  color: #333;
}

.form-group label {
  color: #555;
  margin-bottom: 5px;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}
